@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/Oswald-Regular.woff2") format("woff2"), url("../fonts/oswald/Oswald-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

:root {
  --primary-color: #68b738;
  --special-color: #000000;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}

.title-h1 {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  font-style: normal;
  line-height: 1;
  color: #000000;
}
@media (min-width: 768px) {
  .title-h1 {
    font-size: 60px;
  }
}

.title-h2 {
  margin: 0;
  max-width: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  color: #000000;
}
@media (min-width: 768px) {
  .title-h2 {
    max-width: 100%;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .title-h2 {
    max-width: 1220px;
  }
}

.title-container {
  display: grid;
  grid-template-columns: 0 auto 1fr;
  gap: 20px;
}
@media (min-width: 768px) {
  .title-container {
    grid-template-columns: 30px auto 1fr;
    gap: 40px;
  }
}
@media (min-width: 1440px) {
  .title-container {
    grid-template-columns: auto auto 1fr;
  }
}
.title-container::before, .title-container::after {
  content: "";
  display: block;
  height: 17px;
  border-bottom: 2px solid #68b738;
}
@media (min-width: 768px) {
  .title-container::before, .title-container::after {
    height: 23px;
  }
}
.title-container::before {
  width: 0;
}
@media (min-width: 768px) {
  .title-container::before {
    width: 30px;
  }
}
@media (min-width: 1440px) {
  .title-container::before {
    width: calc(50vw - 619px - 40px);
  }
}

.title-container__inner {
  width: 187px;
}
@media (min-width: 768px) {
  .title-container__inner {
    width: auto;
  }
}

.page {
  height: 100%;
}

.page__body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100%;
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-style: normal;
  color: #444444;
  background: #ffffff;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0;
    max-width: 1220px;
  }
}
@media (min-width: 768px) {
  .container--narrow {
    padding: 0 70px;
  }
}
@media (min-width: 1440px) {
  .container--narrow {
    padding: 0;
  }
}

.header {
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .header {
    border-bottom: none;
    padding-top: 25px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding-top: 56px;
  }
}
.header--index {
  border-bottom: none;
}

.header__container {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
}
@media (min-width: 768px) {
  .header__container {
    grid-template-columns: auto 1fr;
    gap: 20px;
  }
}
@media (min-width: 1440px) {
  .header__container {
    grid-template-columns: repeat(2, 1fr);
  }
}

.header__logo-content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  padding: 13px 0 14px;
}
@media (min-width: 768px) {
  .header__logo-content {
    justify-self: start;
    align-self: start;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 13px;
    padding: 0;
  }
}

.header__logo {
  display: block;
  width: 33px;
  height: 38px;
}
@media (min-width: 768px) {
  .header__logo {
    width: 60px;
    height: 50px;
  }
}
@media (min-width: 1440px) {
  .header__logo {
    width: 70px;
    height: 59px;
  }
}
.header__logo[href]:hover {
  opacity: 0.8;
}
.header__logo[href]:active {
  opacity: 0.6;
}

.header__logo-item--second {
  justify-self: center;
}

.header__logo-text {
  display: block;
  width: 101px;
  height: 18px;
}
@media (min-width: 1440px) {
  .header__logo-text {
    width: 118px;
    height: 21px;
  }
}
.header__logo-text[href]:hover {
  opacity: 0.8;
}
.header__logo-text[href]:active {
  opacity: 0.6;
}

.header__nav {
  grid-column: span 3;
  margin: 0 -20px;
}
@media (min-width: 768px) {
  .header__nav {
    justify-self: end;
    grid-column: 2;
    grid-row: 1;
    margin: 0;
  }
}

.nav {
  position: relative;
}

.nav__button {
  position: absolute;
  top: -43px;
  right: 21px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 24px;
  height: 18px;
  border: none;
  outline: none;
  background: transparent;
}
@media (min-width: 768px) {
  .nav__button {
    display: none;
  }
}
.nav__button:hover {
  cursor: pointer;
  opacity: 0.6;
}
.nav__button:active {
  cursor: pointer;
  opacity: 0.3;
}

.nav__button-icon {
  position: relative;
  display: inline-block;
  vertical-align: bottom;
  width: 24px;
  height: 2px;
  background-color: #222222;
}
.nav__button-icon::before, .nav__button-icon::after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #222222;
}
.nav__button-icon::before {
  top: -8px;
}
.nav__button-icon::after {
  top: 8px;
}

.nav__inner {
  width: 100%;
}

.nav__list {
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .nav__list {
    flex-flow: row wrap;
    justify-content: flex-end;
    align-items: center;
    gap: 34px;
  }
}

.nav__item {
  border-top: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .nav__item {
    border: 0;
  }
}
.nav__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}
@media (min-width: 768px) {
  .nav__item:last-child {
    border: 0;
  }
}

.nav__link {
  display: block;
  width: 100%;
  padding: 22px 0 23px;
  text-align: center;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  color: #444444;
}
@media (min-width: 768px) {
  .nav__link {
    padding: 0;
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .nav__link {
    font-size: 20px;
  }
}
.nav__link:hover {
  opacity: 0.6;
}
.nav__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .nav__link--current {
    padding: 0 0 10px;
    margin: 0 0 -12px;
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1440px) {
  .nav__link--current {
    padding-bottom: 12px;
    margin-bottom: -14px;
  }
}
@media (min-width: 1440px) {
  .nav__link--inverted {
    color: #ffffff;
    border-color: #ffffff;
  }
}

@media (max-width: 767px) {
  .nav--no-js .nav__button {
    display: none;
  }
  .nav--opened .nav__button {
    right: 18px;
  }
  .nav--opened .nav__button-icon {
    background-color: transparent;
  }
  .nav--opened .nav__button-icon::before {
    top: 0;
    width: 25px;
    transform: rotate(45deg);
  }
  .nav--opened .nav__button-icon::after {
    top: 0;
    width: 25px;
    transform: rotate(-45deg);
  }
  .nav--opened .nav__inner {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
    background-color: #ffffff;
  }
  .nav--closed .nav__inner {
    display: none;
  }
  .nav--no-js .nav__inner {
    position: static;
    display: block;
  }
}
.main {
  display: grid;
}

.main__top-section {
  position: relative;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .main__top-section {
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .main__top-section {
    margin-bottom: 82px;
  }
}

.main__programs {
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .main__programs {
    margin-bottom: 45px;
  }
}
@media (min-width: 1440px) {
  .main__programs {
    margin-bottom: 71px;
  }
}

.main__how-works {
  margin-bottom: 49px;
}
@media (min-width: 768px) {
  .main__how-works {
    margin-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .main__how-works {
    margin-bottom: 101px;
  }
}

.main__catalog {
  margin-bottom: 25px;
}
@media (min-width: 768px) {
  .main__catalog {
    margin-bottom: 70px;
  }
}
@media (min-width: 1440px) {
  .main__catalog {
    margin-bottom: 77px;
  }
}

.main__additional-goods {
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .main__additional-goods {
    margin-bottom: 75px;
  }
}
@media (min-width: 1440px) {
  .main__additional-goods {
    margin-bottom: 86px;
  }
}

.main__program-choice {
  margin-bottom: 32px;
}
@media (min-width: 768px) {
  .main__program-choice {
    margin-bottom: 59px;
  }
}
@media (min-width: 1440px) {
  .main__program-choice {
    margin-bottom: 79px;
  }
}

.top-section {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 27px 0 0;
}
@media (min-width: 768px) {
  .top-section {
    padding-top: 0;
  }
}
@media (min-width: 1440px) {
  .top-section {
    position: static;
    padding-bottom: 80px;
  }
}
.top-section::before {
  content: "";
  position: absolute;
  top: 0;
  z-index: -5;
  width: 100%;
  height: 302px;
  background-color: #68b738;
  background-image: linear-gradient(to bottom, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), image-set(url("../img/content/top-cat-mobile.webp") type("image/webp") 1dppx, url("../img/content/top-cat-mobile-2x.webp") type("image/webp") 2dppx, url("../img/content/top-cat-mobile.jpg") type("image/jpg") 1dppx, url("../img/content/top-cat-mobile-2x.jpg") type("image/jpg") 2dppx);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-width: 768px) {
  .top-section::before {
    display: none;
  }
}
@media (min-width: 1440px) {
  .top-section::before {
    left: 50%;
    display: block;
    width: 50%;
    height: 694px;
    background-image: linear-gradient(to bottom, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), image-set(url("../img/content/top-cat-desktop.webp") type("image/webp") 1dppx, url("../img/content/top-cat-desktop-2x.webp") type("image/webp") 2dppx, url("../img/content/top-cat-desktop.jpg") type("image/jpg") 1dppx, url("../img/content/top-cat-desktop-2x.jpg") type("image/jpg") 2dppx);
    background-position: left;
  }
}

.top-section__container {
  display: grid;
  align-items: center;
  width: 100%;
}
@media (min-width: 768px) {
  .top-section__container {
    position: relative;
    justify-content: start;
    padding: 0 120px 526px;
  }
}
@media (min-width: 1440px) {
  .top-section__container {
    grid-template-columns: repeat(2, 1fr);
    padding: 0 80px;
  }
}

.top-section__title {
  margin: 0 0 25px;
  text-align: center;
  color: #ffffff;
}
@media (min-width: 768px) {
  .top-section__title {
    margin: 65px 0 40px;
    text-align: left;
    color: #000000;
  }
}
@media (min-width: 1440px) {
  .top-section__title {
    margin-top: 110px;
    grid-column: 1;
    grid-row: 1;
  }
}

.top-section__subtitle {
  margin: 0 0 29px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
}
@media (min-width: 768px) {
  .top-section__subtitle {
    margin-bottom: 50px;
    text-align: left;
    font-size: 20px;
    color: #000000;
  }
}
@media (min-width: 1440px) {
  .top-section__subtitle {
    margin-bottom: 52px;
    grid-column: 1;
