.button {
  padding: 10px 20px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  background: #68b738;
  border: none;
  outline: none;
}
@media (min-width: 768px) {
