    padding: 14px 26px;
    font-size: 20px;
    line-height: 26px;
  }
}
.button:hover {
  background: #5eaa2f;
  cursor: pointer;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button:focus {
